import tagSexAge from "../common/tag-sex-age.vue"
export default {
	name: "common-list",
	components:{
		tagSexAge
	},
	props: {
		item: Object,
		index: Number
	},
	data() {
		return {
		}
	},
	methods: {
		async guanzhu() {
			let [err,res] = await this.$http.post('/follow',{
				follow_id: this.item.userid
			},{
				token: true,
				checkToken:true,
				checkAuth: true
			});
			//错误处理
			if(!this.$http.errorCheck(err,res)) return;
			//修改数据
			uni.showToast({ title: '关注成功' });
			let resData = {
				type: 'guanzhu',
				userid:this.item.userid,
				data: true
			}
			// 通知父组件
			this.$emit('changeevent',resData);
			// 通知全局修改数据
			uni.$emit('updateData',resData);
		},
		//顶踩
		async caozuo(type){
			let index = (type == 'ding') ? 1 : 2;
			let [err,res] = await this.$http.post('/support',{
				post_id:this.item.id,
				type:index-1
			},{
				token:true,
				checkToken:true,
				checkAuth:true
			});
			if (!this.$http.errorCheck(err,res)) return;
			uni.showToast({ title: "顶成功" });
			// 通知父组件
			let resData = {
				type:"support",
				post_id:this.item.id,
				do:type
			};
			this.$emit('changeevent',resData);
			// 通知全局
			return uni.$emit("updateData",resData);
		},
		openSpace(){
			uni.navigateTo({
				url: '/pages/user-space/user-space?userid='+this.item.userid
			})
		},
		imgdetail(index){
			uni.previewImage({
				current: index,
				indicator: "number",
				urls:this.item.morepic,
				loop:true
			})
		}
	}
}
